<template>
  <div id="app" class="container">
    <!--   -->
    <div class="user">
      <div class="row">
        <div class="col-xs-12 col-sm-12 col-md-6 col-lg-6">
          <el-tabs v-model="activeName" :style="{'background-color':'white'}">
            <!-- ------------------------Sites------------------------------- -->
            <el-tab-pane label="Sites" name="first">
              <button data-toggle="modal" data-target="#myModal">New_users</button>
              <!-- -------------------------------- -->
              <div
                class="modal fade"
                id="myModal"
                tabindex="-1"
                role="dialog"
                aria-labelledby="myModalLabel"
                aria-hidden="true"
              >
                <div class="modal-dialog">
                  <!--  -->
                  <div class="modal-content">
                    <div class="modal-header">
                      <button
                        type="button"
                        class="close"
                        data-dismiss="modal"
                        aria-hidden="true"
                      >&times;</button>
                      <h4 class="modal-title" id="myModalLabel">{{$t('manage.New_users')}}</h4>
                    </div>
                    <div class="modal-body">
                      <!-- elementForm  -->
                      <el-form ref="form" :model="form" label-width="80px">
                        <el-form-item :label="$t('manage.user_name')">
                          <el-input v-model="form.user"></el-input>
                        </el-form-item>
                        <el-form-item :label="$t('manage.user_password')">
                          <el-input v-model="form.password"></el-input>
                        </el-form-item>
                        <el-form-item :label="$t('manage.mailbox')">
                          <el-input v-model="form.email"></el-input>
                        </el-form-item>
                        <el-form-item :label="$t('manage.phone')">
                          <el-input v-model="form.phone"></el-input>
                        </el-form-item>
                        <!--  -->
                        <el-form-item :label="$t('manage.Responsible_site')">
                          <el-select
                            v-model="form.site"
                            multiple
                            filterable
                            allow-create
                            default-first-option
                            :placeholder="$t('manage.Please_select_site')"
                          >
                            <el-option
                              v-for="item in site_list"
                              :key="item.id"
                              :label="item.address"
                              :value="item.id"
                            ></el-option>
                          </el-select>
                        </el-form-item>
                      </el-form>
                    </div>
                    <div class="modal-footer">
                      <button
                        type="button"
                        class="btn btn-default"
                        data-dismiss="modal"
                      >{{$t('manage.shut_down')}}</button>
                      <button
                        type="button"
                        class="btn btn-primary"
                        @click="onSubmit"
                      >{{$t('manage.Submit_changes')}}</button>
                    </div>
                  </div>
                </div>
              </div>
              <!-- -------------------------------- -->
            </el-tab-pane>
            <el-tab-pane label="Sites" name="secend"></el-tab-pane>
          </el-tabs>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  inject: ["reload"], //
  data() {
    return {
      activeName: "first",
      // 
      form: {},
      // 
      site_list: [],
      // 
      user_list: [],
      activeClass: 0,
      activeClass_add: 0,

      user_data: {},
      add_data: {},
      // 
      warning_list: {},
      // ip
      ip_address: "192.168.1.200"
    };
  },

  created() {
    //    
    this.getAddressList();
    this.getUserList();
    // 
    this.get_addressconfig();
  },
  beforeMount() {},
  mounted() {
    // (this.getUserList();users)
    // this.users(this.user_list[0], this.activeClass);
    // this.adds(this.site_list[0], this.activeClass_add);
  },
  beforeUpdate() {
    //
  },
  methods: {
    //  
    onSubmit() {
      var fromData = {
        level: this.form.level,
        site: this.form.site,
        user: this.form.user,
        password: this.form.password,
        email: this.form.email,
        phone: this.form.phone
      };
      newUser(fromData).then(data => {
        alert(data.data);
        this.reload();
        // jquerybootstrap data-dismiss="modal" 
        $("#myModal").modal("hide");
      });
    },
    // 
    getAddressList() {
      // console.log(111111111111)
      newUser_address().then(data => {
        // console.log(2222222222222222)
        this.site_list = data.data;
        // console.log(this.site_list)
        if (data.data !== undefined && data.data.length > 0) {
          // active
          this.add_data = this.site_list[0];
        } else {
          return null;
        }
      });
    },
    //  
    onSubmit01() {
      var fromData01 = {
        addUser: this.form.addUser,
        address: this.form.address,
        ip: this.form.ip,
        add_email: this.form.add_email,
        add_phone: this.form.add_phone
      };
      // console.log(fromData01);
      newAddress(fromData01).then(data => {
        alert(data.data);
        this.reload();
        // jquerybootstrap data-dismiss="modal" 
        $("#myModal_1").modal("hide");
      });
    },
    // 
    getUserList() {
      newAddress_user().then(data => {
        //  
        // console.log(data.data)
        this.user_list = data.data;
        if (data.data !== undefined && data.data.length > 0) {
          // active
          this.user_data = this.user_list[0];
        } else {
          return null;
        }
      });
    },
    // 
    users(user, index) {
      // index
      this.activeClass = index;
      // 1.user_dict{email: ""id: 1level: ""phone: 1314141415site: Array(3)user: "chenjun"}
      // 2.this.user_data
      this.user_data = user;
    },
    // 
    adds(add, index) {
      this.activeClass_add = index;
      this.add_data = add;
      // console.log(this.add_data);
    },
    // 
    onDelete_user(id, user) {
      var _user_obj = { user: user };
      onDelete_user(_user_obj).then(data => {
        // console.log(user_obj);
        alert(data.data);
        this.reload();
      });
    },
    // 
    onDelete_address(id, address) {
      var _address_obj = { id: id };
      // console.log(address_obj);
      onDelete_address(_address_obj).then(data => {
        // console.log(user_obj);
        alert(data.data);
        this.reload();
      });
    },
    // ()
    putUser1(user_obj, user_name) {
      this.form = user_obj;
      // console.log(this.form);
    },
    // 
    putUser() {
      var _fromData = {
        level: this.form.level,
        site: this.form.site,
        user: this.form.user,
        password: this.form.password,
        email: this.form.email,
        phone: this.form.phone
      };
      // console.log(_fromData);
      onModify_user_put(_fromData).then(data => {
        alert(data.data);
        this.reload();
        // jquerybootstrap data-dismiss="modal" 
        $("#modifyUser").modal("hide");
      });
    },
    // ()
    onModify_add(add) {
      this.form = add;
    },
    // 
    putAddress() {
      var _fromData = {
        addUser: this.form.addUser,
        address: this.form.address,
        ip: this.form.ip,
        add_email: this.form.add_email,
        add_phone: this.form.add_phone
      };
      // console.log(_fromData);
      onModify_address_put(_fromData).then(data => {
        alert(data.data);
        this.reload();
        // jquerybootstrap data-dismiss="modal" 
        $("#myModalAddress").modal("hide");
      });
    },
    // 
    get_addressconfig() {
      let ip_address = { ip_address: this.ip_address };
      Warning_config_post(ip_address).then(data => {
        if (data.data !== undefined && data.data.length > 0) {
          this.warning_list = data.data[0];
        } else {
          return null;
        }
        // console.log(data.data);
        // console.log(this.warning_list);
      });
    },
    // 
    putAddressConfig() {
      var _fromData = {
        ip_address: this.ip_address,
        server_address: this.warning_list.server_address,
        port_config: this.warning_list.port_config,
        send_mail: this.warning_list.send_mail,
        password: this.warning_list.password,
        COM_phone: this.warning_list.COM_phone,
        other_config: this.warning_list.other_config
      };
      // console.log(_fromData);
      Warning_config_put(_fromData).then(data => {
        alert(data.data);
        this.reload();
        // jquerybootstrap data-dismiss="modal" 
        $("#myModalAddressConfig").modal("hide");
      });
    }
  }
};
</script>


<style scoped>
/* scoped */
</style>


