import Vue from 'vue'
import locale from 'element-ui/lib/locale'
import VueI18n from 'vue-i18n'
import messages from './langs/index'

Vue.use(VueI18n)
const i18n = new VueI18n({
    locale: localStorage.getItem('lang') || 'en',  // "" localStorage
    messages: messages
})
locale.i18n((key, value) => i18n.t(key, value)) //element

export default i18n


