<template>
  <div class="maps">
    <!-----------------1.-------------------->
    <div class="map" ref="map"></div>
    <!-----------------4.-------------------->
    <div class="map4" ref="map4" v-show="ShowDiv4">
      <div style="font-size:1.8rem;padding:0.5rem 0 0 1rem">
        <p style="color:#a1a1a1;margin: 0 0 1px;">{{ this.site }}</p>
      </div>
      <div style="font-size: 1.2rem;">
        <span style="padding: 0 0 0 1rem;">Status:{{this.status}}</span>
        <span style="padding: 0 0 0 10rem;">Manager:{{this.user}}</span>
      </div>
      <div style="font-size: 1.2rem;padding: 0 0 0.5rem 1rem;" v-show="ShowDiv4_1">
        <p>Address:{{this.address}}</p>
      </div>
      <div style="padding: 0.5rem 0 0.5rem 1rem;" v-show="ShowDiv4_2">
        <input v-model="input2" style="width: 60%;height: 10%;" placeholder="Address:" />
      </div>
      <!--  -->
      <button @click="Details()" v-show="ShowDiv4_1">Details</button>
      <!--  -->
      <button
        @click="Edit()"
        v-show="ShowDiv4_1"
        style="position: absolute;top: 50%; right: 1%;"
      >Cancel</button>
      <!--  -->
      <button @click="Delete()" v-show="ShowDiv4_2">Delete</button>
      <!--  -->
      <button
        @click="Save1()"
        v-show="ShowDiv4_2"
        style="position: absolute;top: 50%;right: 1%;"
      >Submit</button>
    </div>
    <!--  -->
    <div ref="output" style="display:none"></div>
  </div>
</template>


<script>
import {
  CoordinatePost,
  CoordinateGet,
  All_site,
  CoordinateDelete,
  CoordinateUpdate
} from "../Api/api";
import { log } from "util";
export default {
  inject: ["reload"], // 
  props: ["f_site", "f_ip", "f_country", "f_city", "f_address"], //
  data() {
    return {
      site: "",
      status: "",
      user: "",
      address: "",
      input1: "",
      input2: "",
      //
      ShowDiv: false,
      ShowDiv4: false,
      ShowDiv4_1: true,
      ShowDiv4_2: false,
      //  id
      value: "",
      sites_list: [],
      //  map searchManager PointCoordinate
      map: "",
      searchManager: "",
      PointCoordinate: "",
      // 
      coordinate_list: []
    };
  },
  created() {
    // 0 promis()
    let params = {
      // sessionStorage
      ip: JSON.parse(localStorage.getItem("ip"))
    };
    CoordinateGet(params)
      .then(e => {
        this.coordinate_list = e.data;
        // console.log("", this.coordinate_list);
        this.GetMap();
        // console.log("...");
      })
      .catch(e => {
        console.log("coordinate error", e);
        this.GetMap();
      });
    // 1
    this.get_site();
  },
  mounted() {},
  methods: {
    // 1
    get_site() {
      let params = {
        grade: localStorage.getItem("grade"),
        user: localStorage.getItem("user")
      };
      All_site(params).then(e => {
        this.sites_list = e.data;
        // console.log("sites_list:", this.sites_list);
      });
    },
    //2
    GetMap() {
      // 
      let svg = [
        '<svg xmlns="http://www.w3.org/2000/svg" version="1.1" width="100%" height="100%"><g style="cursor:pointer"><circle cx="200" cy="200" r="15" fill="blue" stroke="blue" stroke-width="2" /><circle cx="200" cy="200" r="15" fill="transparent" stroke="blue" stroke-width="2"><animate attributeName="r" type="rotate" from="0" to="30" dur="2s" repeatCount="indefinite" /></circle></g></svg>'
      ];
      let locs = [];
      this.map = new Microsoft.Maps.Map(this.$refs.map, {
        credentials:
          "Aqcwj7BiTxT9aSixmXgEAqP8GmZlRVSSGJLjowkE-oxH15bEMD1x5P6PsdAXk5wS",
        // center: new Microsoft.Maps.Location(47.6149, -122.1941),
        zoom: 15
      });
      this.coordinate_list.forEach(item => {
        // 1
        let center = {
          latitude: item.A,
          longitude: item.B
        };
        // console.log(center)
        // 1.2 locs
        locs.push(center);
        // console.log(center);
        let pin = new Microsoft.Maps.Pushpin(center, {
          
          icon:'svg/xian.svg',
          title: item.site,
          subTitle: item.address,
          // color: "blue",
          // icon: svg.join("")
        });
        // 2
        this.map.entities.push(pin);
        // 3
        Microsoft.Maps.Events.addHandler(pin, "click", () => {
          this.site = item.site;
          this.status = "";
          this.user = "chenjun";
          this.address = item.address;
          this.ShowDiv = false;
          this.ShowDiv4 = true;
          this.ShowDiv4_1 = true;
          this.ShowDiv4_2 = false;
        });
        // 4
        let bounds = Microsoft.Maps.LocationRect.fromLocations(locs);
        this.map.setView({ bounds: bounds });
      });
    },
    // 
    Search() {
      if (!this.searchManager) {
        // 
        Microsoft.Maps.loadModule("Microsoft.Maps.Search", () => {
          this.searchManager = new Microsoft.Maps.Search.SearchManager(
            this.map
          );
          this.Search();
        });
      } else {
        // 
        this.map.entities.clear();
        //dom value
        // let query = this.$refs.searchTbx.value;
        let query = this.f_country + this.f_city + this.f_address;
        // console.log(query);
        this.geocodeQuery(query);
      }
    },
    geocodeQuery(query) {
      console.log(query);
      let searchRequest = {
        where: query,
        callback: r => {
          if (r && r.results && r.results.length > 0) {
            var pin,
              pins = [],
              locs = [],
              output = "Results:<br/>";
            for (var i = 0; i < r.results.length; i++) {
              // --------------------0.--------------------
              if (i == 0) {
                // console.log(r);
                this.PointCoordinate = {
                  name: r.results[0].location.name,
                  A: r.results[0].location.latitude,
                  B: r.results[0].location.longitude
                };
                // console.log(r);
                console.log("xxxxxx", this.PointCoordinate);
                // var infobox = new Microsoft.Maps.Infobox(
                //   r.results[i].location,
                //   {
                //     title: this.f_site
                //   }
                // );
                // // 
                // infobox.setMap(null);
                // // 
                // infobox.setMap(this.map);
              }
              // --------------------1.-----------------------
              pin = new Microsoft.Maps.Pushpin(r.results[i].location, {
                // icon: "../assets/11.svg",
                // anchor: new Microsoft.Maps.Point(10, 40)
                title: r.results[i].location.name,
                text: i + "",
                color: "blue"
              });
              pins.push(pin);
              locs.push(r.results[i].location);
              output += i + ") " + r.results[i].name + "<br/>";
            }
            // 1.1 
            this.map.entities.push(pins);
            // 1.2  
            this.$refs.output.innerHTML = output;
            // --------------------2. --------------------
            // 2.1 
            Microsoft.Maps.Events.addHandler(this.map, "click", e => {
              // console.log(e);
              // console.log(this.map);
              // 
              // 
              // infobox.setMap(null)
              this.map.entities.clear();
              // 
              var loc;
              if (e.targetType == "pin") {
                loc = e.target.getLocation();
                // console.log("", loc);
              }
              //
              else {
                var point = new Microsoft.Maps.Point(e.pageX, e.pageY);
                loc = e.target.tryPixelToLocation(
                  point,
                  Microsoft.Maps.PixelReference.page
                );
                // console.log("", loc);
                // console.log(e.pageX, e.pageY)
              }
              // console.log(this.PointCoordinate);
              let center = new Microsoft.Maps.Location(
                loc.latitude,
                loc.longitude
              );
              // ()
              this.reverseGeocode();
              // console.log(this.map.getCenter())
              // 2.2 
              this.PointCoordinate = {
                A: loc.latitude,
                B: loc.longitude
              };
              let pint = new Microsoft.Maps.Pushpin(center, {
                title: this.f_site,
                color: "blue"
              });
              this.map.entities.push(pint);
            });
            // 
            var bounds;
            if (r.results.length == 1) {
              bounds = r.results[0].bestView;
            } else {
              // 
              bounds = Microsoft.Maps.LocationRect.fromLocations(locs);
            }
            this.map.setView({ bounds: bounds });
          }
        },
        errorCallback: e => {
          // 
          alert("No results found.");
        }
      };
      // 
      // console.log("xxxxxx=>",searchRequest);
      this.searchManager.geocode(searchRequest);
    },
    // 
    reverseGeocode: function() {
      let self = this;
      if (!self.searchManager) {
        Microsoft.Maps.loadModule("Microsoft.Maps.Search", function() {
          self.searchManager = new Microsoft.Maps.Search.SearchManager(
            self.map
          );
          reverseGeocode();
        });
      } else {
        var searchRequest = {
          location: self.map.getCenter(),
          callback: function(r) {
            // alert(r.name);
            self.$emit("ee", r.name);
            // console.log(".....", r);
          },
          errorCallback: function(e) {
            alert("The coordinate point has no corresponding address.");
          }
        };

        //Make the reverse geocode request.
        this.searchManager.reverseGeocode(searchRequest);
      }
    },
    //3 add site
    add() {
      this.ShowDiv4 = false;
      this.ShowDiv = !this.ShowDiv;
    },
    // 4
    Cancel() {
      this.map.entities.clear();
      this.ShowDiv = false;
      this.reload();
    },
    // 5
    Save() {
      console.log(22222);
      console.log("111", this.PointCoordinate);
      // 
      if (!this.f_site) {
        alert("Please enter the site");
      } else {
        if (!this.PointCoordinate.A) {
          console.log(this.PointCoordinate);
          alert("Please add a marker on the map");
        } else {
          console.log(this.PointCoordinate);
          let params = {
            ip: this.f_ip,
            location: this.f_city,
            site: this.f_site,
            status: "normal", // 
            A: this.PointCoordinate.A,
            B: this.PointCoordinate.B,
            address: this.f_country + this.f_city + this.f_address
          };
          CoordinatePost(params).then(e => {
            // 
            // this.$emit('sschildren','ok')
            // alert(e.data);
            // this.reload(); // 
          });
        }
      }
    },
    // 6
    Details() {
      this.$router.push({ name: "Site Detail", params: { site: this.site } });
    },
    // 7
    Edit() {
      this.ShowDiv4 = false;
    },
    // 8
    Delete() {
      let params = { site: this.site };
      CoordinateDelete(params).then(e => {
        alert(e.data);
        this.reload();
      });
    },
    // 9
    Save1() {
      let params = {
        site: this.site,
        address: this.input2
      };
      CoordinateUpdate(params).then(e => {
        alert(e.data);
        this.reload();
      });
    }
  }
};
</script>
<stylescoped lang="less">
//   all print screen   
// 320px iphone5320px
.maps {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 37rem;
}
// ------------------------------------------------------------------

// ------------------------------------------------------------------
/**/
.map {
  /* flex-grow: 1; */
  flex-shrink: 1;
  padding-top: 0;
  height: 37rem;
}
// -----------------------------------------------------------------------
.map2 {
  /* flex-grow: 1; */
  flex-shrink: 1;
  height: 11rem;
  border: 0.1rem solid #a1a1a1;
  /deep/ .el-select .el-input {
    width: 181.5%;
  }
  /deep/ .el-select .el-input__inner {
    border: 0.01rem solid #a1a1a1;
    height: 30px;
    line-height: 30px;
    border-radius: 0;
    margin: 1% 0 0 2%;
  }
  /deep/ .el-input__icon {
    line-height: 33px;
  }
  .input_style {
    width: 74.8%;
    height: 2.8rem;
    border: 0.01rem solid #a1a1a1;
    margin: 1% 0 0 1.5%;
    border-radius: 0;
    height: 31px;
    line-height: 31px;
  }
  button {
    border: 0.09rem solid #04397e;
    border-radius: 0.5rem;
    background-color: transparent;
    outline: none;
    width: 5rem;
    height: 2.8rem;
    color: #04397e;
    font-size: 1.2rem;
    margin: 1% 3% 0 0;
    float: right;
  }
}
/*  */
/* .map2 .select_style:focus {
  background: url(../assets/shangjiantou.png) no-repeat right 1rem center;
  background-size: 2rem 2rem;
} */
// -----------------------------------------------------------------------
.map4 {
  /* flex-grow: 1; */
  position: relative;
  flex-shrink: 1;
  height: 8rem;
  border: 0.1rem solid #a1a1a1;
  button {
    position: absolute;
    top: 4%;
    right: 1%;
    border: 0.09rem solid rgb(4, 57, 126);
    border-radius: 0.5rem;
    background-color: transparent;
    outline: none; /*  */
    width: 6rem;
    height: 2.8rem;
    color: rgb(4, 57, 126);
    font-size: 1.2rem;
    margin: 1% 3% 0 0;
    float: right;
  }
}

/**/
.map3 {
  flex-shrink: 0;
  padding: 1rem 2rem 1rem 0;
  button {
    border: 0.1rem solid #a1a1a1;
    border-radius: 0.5rem;
    background-color: transparent;
    outline: none; /*  */
    width: 8rem;
    height: 3rem;
    float: right;
  }
}
</style>



